h1 {
    color: rgb(37, 40, 219);
    font-style: italic;
}

li {
    color: rgb(71, 66, 66);
    padding-top: 3px;
    font-size: 15px;
}

.note {
    color: rgb(141, 26, 141);
    padding-top: 5px;
    font-size: 15px;
}

.error {
    color: red;
    background: rgb(114, 63, 63);
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}